.wrapper {
    display: flex;
}

.wrapperOpened {
    height: -webkit-fill-available;
}

.jokesWrapper {
    display: none;
}

.jokesWrapperOpened {
    display: block;
}

.sidebar {
    min-width: 480px;
    max-width: 480px;
}

.opened {
    filter: brightness(0.5);
    pointer-events: none;
}

@media screen and (max-width: 1023px) {
    .sidebar {
        display: none;
    }
}
